<template>
    <yd-layout>
        <div class="incomeForm">
            <div class="header">
                <div class="title">收入证明用途填写</div>
                <trsTextarea class="trsTextarea1" v-model="certPurpose" :placeholder="use"></trsTextarea>
            </div>
            <div class="header header2">
                <div class="title">收入证明抬头填写</div>
                <trsTextarea class="trsTextarea2" v-model="certTitle" placeholder="如：xx银行xxx支行"></trsTextarea>
            </div>
            <div class="incomeOption">
                <div class="title">收入证明选项（如有特殊需求请勾选）</div>
                <div class="option" @click="salaryAfterTax=!salaryAfterTax" :class="{option2:salaryAfterTax==true}">税后工资</div>
                <div class="option" @click="incomeAnnual=!incomeAnnual" :class="{option2:incomeAnnual==true}">年平均收入</div>
                <div class="option" @click="fundHouse=!fundHouse" :class="{option2:fundHouse==true}">显示公积金/补充公积金/个人缴纳部分</div>
                <div class="default">默认为税前、月收入、不显示公积金/补充公积金/个人缴纳部分</div>
            </div>
        </div>
        <yd-tabbar slot="tabbar">
            <div class="agree" @click="send()">送人力资源审批</div>
            <div class="back" @click="$router.push('/proofInitiate')">取消</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
// import axios from 'axios';
export default {
    name: 'incomeForm',
    data() {
        return {
            use: '1.用途（购房、买车、信用贷）；\n2.月还款金额（总借贷150万，月还款1.5万）；\n3.还款人员情况（如：夫妻二人，配偶方可开具：0.8万）',
            certTitle: '',
            certPurpose: '',
            salaryAfterTax: false,
            incomeAnnual: false,
            fundHouse: false

        }
    },
    mounted() {},
    computed: {
        ...mapGetters(['getUserInfo'])
    },
    methods: {
        send() {
            let vm = this;
            if (vm.certPurpose == '') {
                vm.$dialog.toast({
                    mes: '请填写收入证明用途',
                    timeout: 2000
                });
            } else if (vm.certTitle == '') {
                vm.$dialog.toast({
                    mes: '请填写收入证明抬头',
                    timeout: 2000
                });
            } else {
                vm.get(vm.conf.portalMobile + '/portal/incomeCertificate', {
                    'certTitle': vm.certTitle,
                    'certPurpose': vm.certPurpose,
                    'salaryAfterTax': vm.salaryAfterTax,
                    'incomeAnnual': vm.incomeAnnual,
                    'fundHouse': vm.fundHouse,
                    'account': vm.getUserInfo.userid,
                    'userId': vm.getUserInfo.empcode,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.$dialog.toast({
                            mes: '发送成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    } else {
                        vm.$dialog.toast({
                            mes: res.errorMessage,
                            timeout: 2000
                        });
                    }
                });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import 'proofOfIncome.less';
</style>


